import Vue from 'vue'
import Router from 'vue-router'
import shopcart from 'pages/shopped'

import App from '@/App'
Vue.use(Router)

const scrollBehavior = (to, from, savedPosition) => {
    if(savedPosition){
        return savedPosition
    }else{
        return { x: 0, y: 0 }
    }
}
export default new Router({
  linkActiveClass: 'active',
  mode: 'history',
  scrollBehavior ,
  routes: [
    {
      path: '',
      name: 'App',
      redirect: "/index",
      component:r => require.ensure([], () => r(require('pages/index')), 'index'),
      meta:{ 
        keepAlive: true,
        title:'首页'
       }
    },
    {
      path: '/index',
      name: 'index',
      component:  r => require.ensure([], () => r(require('pages/index')), 'index'),
      meta:{ 
        keepAlive: true,
        title:'首页'
       }
    },
    {
      path: '/classify',
      name: 'classify',
      component: r => require.ensure([], () => r(require('pages/classify')), 'classify'),
      meta:{ 
        title:'分类'
       }
    },
    {
      path: '/subject_List',
      name: 'subject_List',
      component: r => require.ensure([], () => r(require('pages/subject_List')), 'subject_List'),
      meta:{ 
        title:'人气品牌'
       }
    },
    {
      path: '/classify_goods_list',
      name: 'goods_list',
      component:  r => require.ensure([], () => r(require('pages/goods_list')), 'goods_list'),
      meta:{ 
        title:'品牌分类'
       }
    },
    {
      path: '/classify_goods_type_list',
      name: 'goods_type_list',
      component: r => require.ensure([], () => r(require('pages/goods_type_list')), 'goods_type_list'),
      meta:{ 
        title:'产品分类列表'
       }
    },
    {
      path: '/details_GetGood_Desc',
      name: 'details',
      component: r => require.ensure([], () => r(require('pages/details')), 'details'),
      meta:{ 
        title:'产品详情'
       }
    },
    {
      path: '/shopcart',
      name: 'shopcart',
      component:  r => require.ensure([], () => r(require('pages/shopped')), 'shopcart'),
      meta:{ 
        title:'购物车'
       }
    },
    {
      path: '/user',
      name: 'user',
      component:  r => require.ensure([], () => r(require('pages/user')), 'user'),
      meta:{ 
        title:'用户中心'
       }
    },
    {
      path: '/register',
      name: 'reg',
      component: r => require.ensure([], () => r(require('components/user/register')), 'reg'),
      meta:{ 
        title:'注册中心'
       }
    },
    {
      path: '/login',
      name: 'login',
      component: r => require.ensure([], () => r(require('components/user/login')), 'login'),
      meta:{ 
        title:'登陆中心'
       }
    },
    {
      path: '/search',
      name: 'search',
      component: r => require.ensure([], () => r(require('components/search/search')), 'search'),
      meta:{ 
        title:'搜索中心'
       }
    },
    {
      path:'*',
      name: 'error',
      component: r => require.ensure([], () => r(require('components/common/error')), 'error'),
    }
  ],
})



